.liveApp table {
    font-family: "Fraunces", serif;
    font-size: 100%;
    white-space: nowrap;
    margin: 0;
    border: none;
    border-collapse: separate;
    border-spacing: 0;
    table-layout: fixed;
    border: 1px solid black;
}

.liveApp table td,
th {
    border: 1px solid black;
    padding: 0.5rem 1rem;
}

.liveApp table thead th {
    padding: 3px;
    position: sticky;
    top: 0;
    z-index: 1;
    width: 25vw;
    background: white;
}

.liveApp table td {
    /*background: #fff;*/
    padding: 4px 5px;
    text-align: center;
}

.liveApp table tbody th {
    font-weight: 10;
    font-style: normal;
    text-align: left;
    position: relative;
}

.liveApp table thead th:first-child {
    position: sticky;
    left: 0;
    z-index: 2;
}

.liveApp table tbody th {
    position: sticky;
    left: 0;
    /*background: white;*/
    z-index: 1;
}

.myhover:hover {
    background: grey !important;
}