.App {
  text-align: centeer;
}

.App-logo {
  height: 10vmin;
  pointer-events: none;
  display: inline;
}

.Loading-logo {
  height: 10vmin;
  pointer-events: none;
}


@media (prefers-reduced-motion: no-preference) {
  .Loading-logo {
    animation: App-logo-spin infinite 2s linear;
  }
}

.App-header {
  background-color: #86afff;
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.mynavbar a {
  padding: 10px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}